<template>
  <div class="sidebar">
    <el-menu :default-active="onRoutes" class="el-menu-vertical-demo" theme="dark" unique-opened router>
      <template v-for="item in items">
        <template v-if="item.subs">
          <el-submenu :index="item.index">
            <template slot="title"><i :class="item.icon" id="menu-icon"></i>{{ item.title }}</template>
            <el-menu-item v-for="(subItem,i) in item.subs" :key="i" :index="subItem.index">{{ subItem.title }}
            </el-menu-item>
          </el-submenu>
        </template>
        <template v-else>
          <el-menu-item :index="item.index">
            <i :class="item.icon"></i>{{ item.title }}
          </el-menu-item>
        </template>
      </template>
    </el-menu>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        items: [
          {
            icon: 'el-icon-menu',
            index: 'main',
            title: '主页'
          },
          {
            icon: 'fa fa-desktop',
            index: '2',
            title: '系统管理',
            subs: [
              {
                index: 'role',
                title: '角色管理'
              },
              {
                index: 'permission',
                title: '权限管理'
              }
            ]
          },
          {
            icon: 'fa fa-gear',
            index: '3',
            title: '用户管理',
            subs: [
              {
                index: 'userList',
                title: '用户列表'
              }
            ]
          },
          {
            icon: 'fa fa-area-chart',
            index: '4',
            title: '图表',
            subs:[
              {
                index:'echarts',
                title:'Echarts图表'

              }
            ]
          }

        ]
      }
    },
    computed:{
      onRoutes(){
        return this.$route.path.replace('/','');
      }
    }
  }
</script>

<style scoped>
  .sidebar{
    display: block;
    position: absolute;
    width: 250px;
    left: 0;
    top: 70px;
    bottom:0;
    background: #2E363F;
  }
  .sidebar > ul {
    height:100%;
  }
  #menu-icon{
    vertical-align: middle;
    margin-right: 5px;
    width: 24px;
    text-align: center;
  }
</style>



